import { Component, OnInit } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ToastServices } from '../../../providers/Utilidades/ToastService.service';
import { Router } from '@angular/router';
import { AlertController, ModalController } from '@ionic/angular';
import { PatinadasService } from 'src/app/providers/Api/Patinadas.service';
import { ModalPatinadaComponent } from '../../../Components/modal-patinada/modal-patinada.component';
import { Socket } from 'ngx-socket-io';

@Component({
  selector: 'app-patinadas-inicio',
  templateUrl: './patinadas-inicio.page.html',
  styleUrls: ['./patinadas-inicio.page.scss'],
})
export class PatinadasInicioPage implements OnInit {

  patinadas = [];
  cargado = false;

  constructor(
    private almacenamiento: InicioService,
    private toast: ToastServices,
    private router: Router,
    private alert: AlertController,
    private apiPatinada: PatinadasService,
    private modal: ModalController,
    private socket: Socket
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    
    this.traer();

  }

  refrescando(event) {
    this.traer().then(() => {
      event.detail.complete();
    }).catch(() => {
      event.detail.complete();
    })
  }

  async traer() {
    this.patinadas = [];

    const info = await this.almacenamiento.obtenerToken();


    if (info ) {

      this.apiPatinada.consultarPatinada(info.token).subscribe((data: any) => {
        console.log(data, 'wu')
        if (data.exe) {
          data.response.forEach((item) => {
            item.vs = false;
            this.patinadas.push(item);
          })
          this.cargado = true;
        } else {
          this.toast.toastError('No se pudo consultar las patinadas, inténtelo más tarde');
          this.router.navigate(['/central/menu']);
        }
      }, (err) => {
        this.toast.toastError('No se pudo consultar las patinadas, inténtelo más tarde');
        this.router.navigate(['/central/menu']);
      })
    }

  }

  async crear() {

    const modal = await this.modal.create({
      component: ModalPatinadaComponent
    })

    await modal.present();

    const cerrar = await modal.onWillDismiss();

    if (cerrar) {
      if (cerrar.data) {
        this.router.navigate(['/central/mispatinadas']);
      }
    }

  }

  async ayudar(patinada) {

    const alert = await this.alert.create({
      header: 'Atención!',
      // tslint:disable-next-line:max-line-length
      message: '¿Quieres colaborarle a ' + patinada.creador.nombrecompleto + ' en su patinada? al aceptar se creará una sala de chat para que puedan hablar del asunto.',
      buttons: [
        {
          text: 'No Acepto',
          cssClass: 'cancelarAlerta',
          role: 'cancel',
          handler: () => {
          }
        },
        {
          text: 'Acepto',
          cssClass: 'botonalerta',
          handler: async () => {
            const info = await this.almacenamiento.obtenerToken();


            if (info ) {
        
              this.apiPatinada.patinadaEditar(info.token, {id: patinada._id}).subscribe((data: any) => {
                console.log(data, 'wu')
                if (data.exe) {
                  this.socket.emit('patinadasayuda', {
                    nombre: info.nombre,
                    usuario: data.response.creador._id
                  })
                  this.router.navigate(['/central/mispatinadas']);
                } else {
                  this.toast.toastError('No se pudo consultar las patinadas, inténtelo más tarde');
                  this.router.navigate(['/central/menu']);
                }
              }, (err) => {
                this.toast.toastError('No se pudo consultar las patinadas, inténtelo más tarde');
                this.router.navigate(['/central/menu']);
              })
            }
          }
        }
      ]
    })

    await alert.present();
   

  }

  mostrar(i) {
    this.patinadas[i].vs = !this.patinadas[i].vs;
  }

}
